<template>
  <v-app-bar
    :collapse-on-scroll="$vuetify.breakpoint.smAndDown"
    fixed
    :color="night"
    dark
    :elevate-on-scroll="$vuetify.breakpoint.mdAndUp"
  >
    <v-menu transition="slide-y-transition" bottom offset-y>
      <template v-slot:activator="{on:menu}">
        <v-app-bar-nav-icon v-on="menu" v-show="$vuetify.breakpoint.smAndDown"></v-app-bar-nav-icon>
      </template>
      <v-list>
        <v-list-item @click="See('https://www.youtube.com/channel/UCoztvTULBYd3WmStqYeoHcA')">
          <v-list-item-title>
            <v-icon left color="red">mdi-youtube</v-icon>笹木咲 Ch. Sasaki Saku
          </v-list-item-title>
        </v-list-item>
        <v-list-item @click="See('https://twitter.com/saku_sasaki')">
          <v-list-item-title>
            <v-icon left color="blue">mdi-twitter</v-icon>@saku_sasaki
          </v-list-item-title>
        </v-list-item>
        <v-list-item @click="See('https://space.bilibili.com/436596839')">
          <v-list-item-title>
            <v-icon left color="#f69" class="iconfont icon-bilibili"></v-icon>笹木咲Official
          </v-list-item-title>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item @click="See('https://github.com/Nyaasu66/sasa-button')">
          <v-list-item-title>
            <v-icon left color="black">mdi-github</v-icon>
            {{$t("ui.helpdevelope")}}
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <v-toolbar-title>{{$t("ui.title")}}</v-toolbar-title>
    <v-btn
      v-if="$vuetify.breakpoint.mdAndUp"
      class="ml-4 text-capitalize"
      color="red"
      @click="See('https://www.youtube.com/channel/UCoztvTULBYd3WmStqYeoHcA')"
    >
      <v-icon left>mdi-youtube</v-icon>笹木咲 Ch. Sasaki Saku
    </v-btn>
    <v-btn
      v-if="$vuetify.breakpoint.mdAndUp"
      class="ml-4 text-lowercase"
      color="blue"
      @click="See('https://twitter.com/saku_sasaki')"
    >
      <v-icon left>mdi-twitter</v-icon>@saku_sasaki
    </v-btn>
    <v-btn
      v-if="$vuetify.breakpoint.mdAndUp"
      class="ml-4 text-lowercase"
      color="#f69"
      @click="See('https://space.bilibili.com/436596839')"
    >
      <v-icon left class="iconfont icon-bilibili"></v-icon>笹木咲Official
    </v-btn>
    <v-spacer></v-spacer>
    <v-btn
      v-if="$vuetify.breakpoint.mdAndUp"
      @click="See('https://github.com/Nyaasu66/sasa-button')"
    >
      <v-icon left>mdi-github</v-icon>
      {{$t("ui.helpdevelope")}}
    </v-btn>
    <v-btn v-on="on" fab icon @click="DarkMode()">
      <v-icon>{{darkmodeicon}}</v-icon>
    </v-btn>
    <v-menu transition="slide-y-transition" bottom offset-y>
      <template v-slot:activator="{on:menu}">
        <v-fab-transition>
          <v-btn v-on="menu" fab icon>
            <v-icon>mdi-earth</v-icon>
          </v-btn>
        </v-fab-transition>
      </template>
      <v-list>
        <v-list-item
          v-for="(lang, i) in langs"
          :key="i"
          :disabled="!lang.isready"
          @click="ChangeLang(lang.src)"
        >
          <v-list-item-title>{{ lang.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
export default {
  data: () => ({
    langs: [
      { title: "简体中文", src: "zhHans", isready: true },
      { title: "English", src: "en", isready: true },
      { title: "日本語(ベータ版)", src: "ja", isready: true }
    ]
  }),
  methods: {
    ChangeLang(e) {
      this.$i18n.locale = e;
      this.$cookies.set("Lang", e);
    },
    DarkMode() {
      this.$store.commit("change_dark_mode");
      this.$vuetify.theme.dark = this.$store.state.dark_mode;
    },
    See(e) {
      window.location.href = e;
    }
  },
  computed: {
    darkmodeicon: {
      get: function() {
        return this.$store.state.dark_icon;
      }
    },
    night: {
      get: function() {
        if (this.$vuetify.theme.dark) {
          return "grey darken-4";
        } else {
          return "primary";
        }
      }
    }
  }
};
</script>

<style>
@font-face {font-family: "iconfont";
  src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2')
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 20px !important;
  font-style: normal;
